// @format

import React, { useReducer, useMemo, useCallback, useState } from 'react'
import { useLocalStorageState, useLocalStorageReducer } from './hooks'

import Config from './Config'
import Wheel from './Wheel'
import Solution from './Solution'
import Progress from './Progress'
import Modal from './Modal'

import colors from 'colors.js'

import solutions from './solutions'
import { contrastColor } from './util'

const solutionContrasts = solutions.void.reduce(
	(map, [, { color }]) => ({
		...map,
		[color]: contrastColor(colors.name2rgb(color)),
	}),
	{},
)

const App = () => {
	const [showInstructions, setShowInstructions] = useState(false)

	const [wheels, setWheels] = useReducer(
		(wheels, action) => Object.values({ ...wheels, ...action }),
		4,
		(len) => new Array(len).fill(null),
	)

	const [config, setConfig] = useLocalStorageState('config', 'void')
	const [consoleNumbers, setConsoleNumbers] = useLocalStorageReducer(
		'consoleNumbers',
		(numbers, action) => ({ ...numbers, ...action }),
		[1, 2],
	)

	const chosenSolutions = useMemo(
		() =>
			solutions[config].reduce((sol, [k, v]) => {
				let terms = k.split(':')
				return {
					...sol,
					[`${terms[parseInt(consoleNumbers[0], 10) - 1]}-${
						terms[parseInt(consoleNumbers[1], 10) - 1]
					}`]: v,
				}
			}, {}),
		[config, consoleNumbers],
	)

	const [locked, dispatchLocked] = useLocalStorageReducer(
		'locked',
		(locked, action) => {
			switch (action.type) {
				case 'add':
					return {
						...locked,
						[`${action.solution.color}-${action.solution.number}`]: true,
					}

				case 'remove': {
					let key = `${action.solution.color}-${action.solution.number}`
					return Object.keys(locked).reduce(
						(obj, k) => (k !== key ? { ...obj, [k]: true } : obj),
						{},
					)
				}

				case 'clear':
					return {}

				default:
					throw new Error(`Unknown action type: ${action.type}`)
			}
		},
		{},
	)

	const [lastLocked, dispatchLastLocked] = useLocalStorageReducer(
		'lastLocked',
		(lastLocked, action) => {
			switch (action.type) {
				case 'push':
					return [...lastLocked, action.solution]

				case 'pop':
					return lastLocked.slice(0, lastLocked.length - 1)

				case 'clear':
					return []

				default:
					throw new Error(`Unknown action type: ${action.type}`)
			}
		},
		[],
	)

	const isLocked = useCallback(
		(solution) =>
			solution != null &&
			locked[`${solution.color}-${solution.number}`] != null,
		[locked],
	)

	const enabled = useMemo(
		() =>
			new Array(4).fill(null).map((_, i) =>
				Object.keys(chosenSolutions)
					.filter((key) => !isLocked(chosenSolutions[key]))
					.map((k) => k.split('-').map((v) => parseInt(v, 10)))
					.filter((parts) =>
						parts.slice(0, i).every((part, i) => part === wheels[i]),
					)
					.map((v) => v[i]),
			),
		[chosenSolutions, wheels, isLocked],
	)

	const solution = useMemo(
		() =>
			chosenSolutions[wheels.join('-')] || {
				color: 'grey',
				number: 0,
			},
		[chosenSolutions, wheels],
	)

	return (
		<div
			style={{ display: 'flex', flexDirection: 'column', padding: '0px 8px' }}
		>
			<div
				style={{
					display: 'flex',
					flexDirection: 'row',
					justifyContent: 'space-around',
					flexWrap: 'wrap',
				}}
			>
				<div style={{ display: 'flex', flexDirection: 'column' }}>
					<h3>选择本周焦灼（虚空/电弧/烈日）</h3>
					<Config
						selected={config}
						onSelect={(id) => {
							setConfig(id)

							if (
								Object.keys(locked).length === 0 ||
								!window.confirm('是否要重置拼图状态？')
							) {
								return
							}

							dispatchLocked({ type: 'clear' })
						}}
					/>

					<h3>
						控制台{' '}
						<select
							value={consoleNumbers[0]}
							onChange={(ev) => setConsoleNumbers({ '0': ev.target.value })}
						>
							{[1, 2, 3]
								.filter((v) => parseInt(consoleNumbers[1], 10) !== v)
								.map((v, i) => (
									<option key={i} value={v}>
										{v}
									</option>
								))}
						</select>
					</h3>
					<div
						style={{
							display: 'flex',
							flexDirection: 'row',
							alignItems: 'start',
							minWidth: 300,
							maxWidth: 500,
						}}
					>
						<Wheel
							style={{ flex: '0 1 100%' }}
							enabled={enabled[0]}
							selected={wheels[0]}
							onSelect={(i) => setWheels({ '0': i })}
						/>
						<Wheel
							style={{ flex: '0 .8 100%' }}
							enabled={enabled[1]}
							selected={wheels[1]}
							onSelect={(i) => setWheels({ '1': i })}
						/>
					</div>

					<h3>
						控制台{' '}
						<select
							value={consoleNumbers[1]}
							onChange={(ev) => setConsoleNumbers({ '1': ev.target.value })}
						>
							{[1, 2, 3]
								.filter((v) => parseInt(consoleNumbers[0], 10) !== v)
								.map((v, i) => (
									<option key={i} value={v}>
										{v}
									</option>
								))}
						</select>
					</h3>
					<div
						style={{
							display: 'flex',
							flexDirection: 'row',
							alignItems: 'start',
							minWidth: 300,
							maxWidth: 500,
						}}
					>
						<Wheel
							style={{ flex: '0 1 100%' }}
							enabled={enabled[2]}
							selected={wheels[2]}
							onSelect={(i) => setWheels({ '2': i })}
						/>
						<Wheel
							style={{ flex: '0 .8 100%' }}
							enabled={enabled[3]}
							selected={wheels[3]}
							onSelect={(i) => setWheels({ '3': i })}
						/>
					</div>
				</div>

				<div
					style={{
						display: 'flex',
						flexDirection: 'column',
						flex: '0 1 400px',
					}}
				>
					<h3>
						解密答案{' '}
						{solution.number > 0 ? (
							locked[`${solution.color}-${solution.number}`] == null ? (
								<span
									style={{
										color: solution.color,
										backgroundColor: solutionContrasts[solution.color],
										padding: 8,
										borderRadius: 8,
									}}
								>
									{`${solution.color[0].toUpperCase()}${solution.color.substring(
										1,
									)}`}{' '}
									{solution.number}
								</span>
							) : (
								<span style={{ color: 'blue', padding: 8 }}>
									之前找到过
								</span>
							)
						) : (
							<span style={{ color: 'red', padding: 8 }}>没找到</span>
						)}
					</h3>

					<div
						style={{
							display: 'flex',
							flexDirection: 'row',
							justifyContent: 'space-around',
						}}
					>
						<button
							style={{
								color: 'white',
								backgroundColor: solution.number > 0 ? 'blue' : 'lightblue',
							}}
							disabled={solution.number === 0}
							onClick={() => {
								dispatchLocked({ type: 'add', solution })
								dispatchLastLocked({ type: 'push', solution })
							}}
						>
							锁定序列
						</button>

						<button
							disabled={lastLocked.length === 0}
							onClick={() => {
								dispatchLocked({
									type: 'remove',
									solution: lastLocked[lastLocked.length - 1],
								})
								dispatchLastLocked({ type: 'pop' })
							}}
						>
							撤销
						</button>

						<button
							disabled={Object.keys(locked).length === 0}
							onClick={() => {
								if (
									!window.confirm(
										'确定要重置拼图状态吗？',
									)
								) {
									return
								}

								dispatchLocked({ type: 'clear' })
								dispatchLastLocked({ type: 'clear' })
							}}
						>
							重置
						</button>
					</div>

					<Progress
						style={{
							backgroundColor: 'lightgrey',
							marginTop: 16,
						}}
						value={Object.keys(locked).length}
						max={49}
					/>

					<Solution
						style={{
							marginTop: 16,
						}}
						color={solution.color}
						number={solution.number}
						locked={locked}
					/>
				</div>
			</div>

			<div
				style={{
					display: 'flex',
					flexDirection: 'row',
					justifyContent: 'flex-start',
					alignItems: 'center',
					flexWrap: 'wrap',
					marginTop: 16,
				}}
			>
				<button
					style={{ marginRight: '1em' }}
					onClick={() => setShowInstructions(true)}
				>
					显示说明
				</button>

				<div style={{ margin: '1em 0px' }}>
					由DeedleFake创造，
					<a href="https://space.bilibili.com/9656618" target="__blank__">阿松呀</a>
					汉化，灵感来自于{' '}
					<a href="https://dm.reddit.com/r/raidsecrets/comments/bmi7fv/void_configuration_solution_solver_mobile_support/emwtklf/">
						这个评论
					</a>
					.
				</div>
			</div>

			<Modal open={showInstructions} onClose={() => setShowInstructions(false)}>
				<h4>使用说明</h4>

				<p style={{ maxWidth: 600 }}>
					To use, simply click the numbers on the corresponding consoles that
					are highlighted in game. Once enough numbers have been entered to
					determine a solution, a terminal in one of the colored rooms will be
					highlighted in the solution diagram. Simply go to the corresponding
					terminal in game and lock the sequence.
				</p>

				<p style={{ maxWidth: 600 }}>
				要使用，只需单击游戏中突出显示的相应控制台上的数字。一旦输入足够的数字来确定解密答案，其中一个彩色房间中的终端将在右侧答案图中突出显示。只需到游戏中相应的终端并锁定序列。
				</p>

				<p style={{ maxWidth: 600 }}>
					As an extra convienence, clicking the <code>Lock Sequence</code>{' '}
					button below the solution will mark the current terminal as having
					been locked, removing it from the map and removing the terminal
					sequences for that solution from the possibilities.
				</p>
				
				<p style={{ maxWidth: 600 }}>
				为方便起见，单击解密答案下方的<code>锁定序列</code>按钮将标记当前终端已被锁定，将其从地图中删除，并从可能性中删除该解密答案的终端序列。
				</p>
				

			</Modal>
		</div>
	)
}

export default App
